<template>
  <div id="app" class="ppt-generator" :class="{ 'light-mode': isLightMode }">
    <!-- Êñ∞ÁöÑÊ†áÈ¢òËÆæËÆ° -->
    <div class="title-section">
      <h2 class="gradient-title">
        ‰∏ÄÈîÆÁîüÊàê<span class="ppt-text">PPT</span
        ><span class="star-icon">‚ú®</span>
      </h2>
      <p class="subtitle">ÊîØÊåÅÂØºÂÖ•ÊñáÊ°£„ÄÅÂà©Áî®‰∏™ÊÄßÂåñÊ®°ÊùøÂàõ‰ΩúPPT</p>
    </div>

    <!-- ÂÜÖÂÆπËæìÂÖ•Âå∫ (ÁΩÆ‰∫éÈ°∂ÈÉ®) -->
    <div class="main-content">
      <!-- Êñá‰ª∂‰∏ä‰º†‰∏éÊñáÊú¨ËæìÂÖ•Âå∫Âüü -->
      <div class="input-area">
        <div class="input-container">
          <!-- Â¶ÇÊûúÊúâÊñá‰ª∂ÔºåÊòæÁ§∫Êñá‰ª∂‰ø°ÊÅØ -->
          <div class="file-upload-container" v-if="selectedFile">
            <div class="file-info">
              <i class="file-icon">üìÑ</i>
              <span class="file-name-display">{{ selectedFile.name }}</span>
              <button class="clear-file-btn" @click="clearFile">√ó</button>
            </div>
          </div>

          <!-- Â¶ÇÊûúÊ≤°ÊúâÊñá‰ª∂ÔºåÊòæÁ§∫ÊñáÊú¨ËæìÂÖ•Ê°Ü -->
          <textarea
            v-if="!selectedFile"
            v-model="description"
            placeholder="ËØïËØïËæìÂÖ•‰∏ÄÁÇπÁÇπÁöÑÂ∞èÂ∞èÁöÑÈúÄÊ±ÇÂç≥ÂèØÁîüÊàêPPT"
          ></textarea>

          <div class="input-actions">
            <div class="action-buttons">
              <input
                type="file"
                ref="fileInput"
                @change="handleFileUpload"
                accept=".pdf,.doc,.docx,.txt,.md"
                hidden
              />
              <button @click="triggerFileUpload" class="action-btn upload-btn">
                <i class="action-icon">üìÑ</i
                >{{ selectedFile ? "ÈáçÊñ∞‰∏ä‰º†" : "‰∏ä‰º†ÊñáÊ°£" }}
              </button>
            </div>
            <button
              class="create-btn"
              :disabled="
                loading || (!selectedFile && !description) || !selectedTemplate
              "
              @click="generatePPT"
            >
              {{ loading ? "ÁîüÊàê‰∏≠..." : "Á´ãÂç≥Âàõ‰Ωú" }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Ê®°ÊùøÈÄâÊã©Âå∫Âüü (ÁßªÂà∞‰∏ãÊñπ) -->
    <div class="template-section">
      <!-- Ê®°ÊùøÁ≠õÈÄâÂå∫Âüü -->
      <div class="section-header">
        <h3 class="section-title">ÈÄâÊã©Ê®°Êùø</h3>
        <div class="filter-group">
          <select v-model="selectedStyle" @change="handleFilterChange">
            <option value="">ÊâÄÊúâÈ£éÊ†º</option>
            <option v-for="style in styleOptions" :key="style" :value="style">
              {{ style }}
            </option>
          </select>

          <select v-model="selectedColor" @change="handleFilterChange">
            <option value="">ÊâÄÊúâÈ¢úËâ≤</option>
            <option v-for="color in colorOptions" :key="color" :value="color">
              {{ color }}
            </option>
          </select>
        </div>
      </div>

      <div class="template-grid">
        <div v-if="loadingTemplates" class="loading-tip">Ê®°ÊùøÂä†ËΩΩ‰∏≠...</div>
        <div
          v-if="!loadingTemplates && templates.length === 0"
          class="empty-tip"
        >
          {{
            hasActiveFilters
              ? "ÂΩìÂâçÁ≠õÈÄâÊù°‰ª∂‰∏ãÊöÇÊó†Ê®°ÊùøÔºåËØ∑Â∞ùËØïÂÖ∂‰ªñÊù°‰ª∂"
              : "ÊöÇÊó†ÂèØÁî®Ê®°Êùø"
          }}
          <span
            v-if="hasActiveFilters"
            class="clear-filter"
            @click="clearFilters"
          >
            ÔºàÊ∏ÖÁ©∫Á≠õÈÄâÊù°‰ª∂Ôºâ
          </span>
        </div>
        <div
          v-for="template in templates"
          :key="template.id"
          class="template-item"
          :class="{ selected: selectedTemplate === template.id }"
          @click="selectTemplate(template.id)"
        >
          <img
            :src="template.cover || '/default-cover.png'"
            :alt="template.name"
            class="template-cover"
            @error="handleImageError"
          />
          <div class="template-selected-indicator">
            <span class="checkmark">‚úì</span>
          </div>
        </div>
      </div>

      <!-- ÂàÜÈ°µÊéß‰ª∂ -->
      <div class="pagination">
        <button
          @click="changePage(1)"
          :disabled="currentPage === 1 || totalPages === 0"
        >
          È¶ñÈ°µ
        </button>
        <button
          @click="changePage(currentPage - 1)"
          :disabled="currentPage === 1 || totalPages === 0"
        >
          ‰∏ä‰∏ÄÈ°µ
        </button>

        <span
          v-for="page in visiblePages"
          :key="page"
          @click="changePage(page)"
          :class="{ active: currentPage === page }"
        >
          {{ page }}
        </span>

        <button
          @click="changePage(currentPage + 1)"
          :disabled="currentPage === totalPages || totalPages === 0"
        >
          ‰∏ã‰∏ÄÈ°µ
        </button>
        <button
          @click="changePage(totalPages)"
          :disabled="currentPage === totalPages || totalPages === 0"
        >
          Â∞æÈ°µ
        </button>
      </div>
    </div>

    <!-- ÁªìÊûúÂ±ïÁ§∫ -->
    <div v-if="resultUrl" class="result">
      <p>ÁîüÊàêÂÆåÊàêÔºÅ<a :href="resultUrl" download>ÁÇπÂáª‰∏ãËΩΩPPT</a></p>
    </div>

    <!-- ÈîôËØØÊèêÁ§∫ -->
    <div v-if="error" class="error">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { ref, onMounted, computed } from "vue";
import axios from "axios";

export default {
  name: "App",
  setup() {
    // ÈÖçÁΩÆaxiosÂü∫Á°ÄË∑ØÂæÑ
    axios.defaults.baseURL = "http://localhost:2200/ppt";

    // ÂìçÂ∫îÂºèÊï∞ÊçÆ
    const description = ref("");
    const loading = ref(false);
    const resultUrl = ref("");
    const error = ref("");
    const selectedTemplate = ref(null);
    const templates = ref([]);
    const loadingTemplates = ref(true);
    const taskId = ref(null);
    const fileInput = ref(null);
    const selectedFile = ref(null);

    // ÂàÜÈ°µÁõ∏ÂÖ≥
    const currentPage = ref(1);
    const totalPages = ref(0);
    const pageSize = ref(8);

    // Á≠õÈÄâÁõ∏ÂÖ≥
    const selectedStyle = ref("");
    const selectedColor = ref("");
    const styleOptions = ref([
      "ÁÆÄÁ∫¶",
      "Âç°ÈÄö",
      "ÂïÜÂä°",
      "ÂàõÊÑè",
      "ÂõΩÈ£é",
      "Ê∏ÖÊñ∞",
      "ÊâÅÂπ≥",
      "ÊèíÁîª",
      "ËäÇÊó•",
    ]);
    const colorOptions = ref([
      "ËìùËâ≤",
      "ÁªøËâ≤",
      "Á∫¢Ëâ≤",
      "Á¥´Ëâ≤",
      "ÈªëËâ≤",
      "ÁÅ∞Ëâ≤",
      "ÈªÑËâ≤",
      "Á≤âËâ≤",
      "Ê©ôËâ≤",
    ]);

    // ËÆ°ÁÆóÂ±ûÊÄß
    const visiblePages = computed(() => {
      if (totalPages.value <= 5) {
        return Array.from({ length: totalPages.value }, (_, i) => i + 1);
      }
      const start = Math.max(1, currentPage.value - 2);
      const end = Math.min(totalPages.value, currentPage.value + 2);
      return Array.from({ length: end - start + 1 }, (_, i) => start + i);
    });

    const hasActiveFilters = computed(() => {
      return selectedStyle.value || selectedColor.value;
    });

    // ÊñπÊ≥ï
    const triggerFileUpload = () => {
      fileInput.value.click();
    };

    const handleFileUpload = (e) => {
      const files = e.target.files;
      if (files && files[0]) {
        selectedFile.value = files[0];
        description.value = "";
      } else {
        selectedFile.value = null;
      }
    };

    const fetchTemplates = async () => {
      try {
        loadingTemplates.value = true;
        templates.value = [];
        const response = await axios.get("/templates", {
          params: {
            currentPage: currentPage.value,
            pageSize: pageSize.value,
            style: selectedStyle.value,
            color: selectedColor.value,
          },
        });

        if (response.data.code === 0) {
          templates.value = response.data.data.list;
          totalPages.value = Math.ceil(
            response.data.data.total / pageSize.value
          );
        } else {
          error.value = "Ê®°ÊùøÂä†ËΩΩÂ§±Ë¥•Ôºö" + response.data.error;
        }
      } catch (err) {
        error.value = `Ê®°ÊùøÂä†ËΩΩÂ§±Ë¥•: ${err.message}`;
      } finally {
        loadingTemplates.value = false;
      }
    };

    const changePage = (newPage) => {
      newPage = Math.max(1, Math.min(newPage, totalPages.value));
      if (newPage !== currentPage.value) {
        currentPage.value = newPage;
        fetchTemplates();
      }
    };

    const handleImageError = (e) => {
      e.target.src = "/default-cover.png";
    };

    const selectTemplate = (templateId) => {
      selectedTemplate.value = templateId;
    };

    const handleFilterChange = () => {
      currentPage.value = 1;
      fetchTemplates();
    };

    const clearFilters = () => {
      selectedStyle.value = "";
      selectedColor.value = "";
      handleFilterChange();
    };

    const clearFile = () => {
      selectedFile.value = null;
      fileInput.value.value = "";
    };

    const checkStatus = async () => {
      try {
        const response = await axios.get(`/status/${taskId.value}`);
        const data = response.data.data;

        if (!data) throw new Error("Êó†ÊïàÁöÑÂìçÂ∫îÊ†ºÂºè");
        if (
          data.pptStatus === "build_failed" ||
          data.aiImageStatus === "build_failed" ||
          data.cardNoteStatus === "build_failed"
        ) {
          throw new Error(data.errMsg || "PPTÁîüÊàêÂ§±Ë¥•");
        }

        if (
          data.pptStatus === "done" &&
          data.aiImageStatus === "done" &&
          data.cardNoteStatus === "done"
        ) {
          resultUrl.value = data.pptUrl;
          loading.value = false;
        } else {
          setTimeout(checkStatus, 2000);
        }
      } catch (err) {
        error.value = `ÁîüÊàêÂ§±Ë¥•Ôºö${err.message}`;
        loading.value = false;
        taskId.value = null;
      }
    };

    const generatePPT = async () => {
      if (!selectedTemplate.value) {
        error.value = "ËØ∑ÂÖàÈÄâÊã©Ê®°Êùø";
        return;
      }

      try {
        loading.value = true;
        error.value = "";
        resultUrl.value = "";
        const formData = new FormData();
        formData.append("templateId", selectedTemplate.value);

        if (selectedFile.value) {
          formData.append("file", selectedFile.value);
          formData.append("fileName", selectedFile.value.name);
        } else {
          if (!description.value.trim()) {
            throw new Error("ËØ∑ËæìÂÖ•ÂÜÖÂÆπÊèèËø∞ÊàñÈÄâÊã©Êñá‰ª∂");
          }
          formData.append("query", description.value);
        }

        const response = await axios.post("/generate", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        });

        if (response.data?.code === 0) {
          taskId.value = response.data.task_id;
          checkStatus();
        } else {
          error.value = response.data?.error || "ÁîüÊàêËØ∑Ê±ÇÂ§±Ë¥•";
          loading.value = false;
        }
      } catch (err) {
        error.value = `ËØ∑Ê±ÇÂ§±Ë¥•Ôºö${err.message}`;
        if (err.response) {
          error.value += ` (Áä∂ÊÄÅÁ†ÅÔºö${err.response.status})`;
        }
        loading.value = false;
      }
    };

    onMounted(fetchTemplates);

    return {
      description,
      loading,
      resultUrl,
      error,
      templates,
      selectedTemplate,
      selectTemplate,
      loadingTemplates,
      visiblePages,
      currentPage,
      totalPages,
      changePage,
      handleImageError,
      selectedStyle,
      selectedColor,
      styleOptions,
      colorOptions,
      handleFilterChange,
      hasActiveFilters,
      clearFilters,
      fileInput,
      selectedFile,
      triggerFileUpload,
      handleFileUpload,
      generatePPT,
      clearFile,
    };
  },
  data() {
    return {
      isLightMode: localStorage.getItem("theme-mode") === "light",
    };
  },
  mounted() {
    this.checkThemeMode();
    window.addEventListener("storage", this.checkThemeMode);
    window.addEventListener("themeChange", this.checkThemeMode);
  },
  beforeUnmount() {
    window.removeEventListener("storage", this.checkThemeMode);
    window.removeEventListener("themeChange", this.checkThemeMode);
  },
  methods: {
    checkThemeMode() {
      this.isLightMode = localStorage.getItem("theme-mode") === "light";
    },
  },
};
</script>

<style scoped>
/* Á°Æ‰øùÂç†Êª°Êï¥‰∏™ÂèØÁî®Á©∫Èó¥ */
